@import url(https://fonts.googleapis.com/css?family=Orbitron&display=swap);
@font-face {
  font-family: LiberationSerif-Regular;
  src: url(/numbers/static/media/LiberationSerif-Regular.671c0f4d.ttf);
}

.table {
  width: 74vw;
  height: 100%;

  margin: 0 auto;

  position: relative;
  -webkit-perspective: 2500px;
          perspective: 2500px;

  background-color: #000;
}

.book {
  width: 100%;
  height: 100%;

  position: absolute;
  -webkit-perspective: 2500px;
          perspective: 2500px;
  -webkit-transform: rotateX(10deg) translateX(-25%) translateY(-120%);
          transform: rotateX(10deg) translateX(-25%) translateY(-120%);
  -webkit-animation: bookMove 4s ease;
          animation: bookMove 4s ease;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;

  font-family: LiberationSerif-Regular;
  font-size: 5.5vh;
  line-height: 5.7vh;

  background-color: #000;
}

@-webkit-keyframes bookMove {
  30% {
    -webkit-transform: rotateX(10deg) translateX(-25%) translateY(0);
            transform: rotateX(10deg) translateX(-25%) translateY(0);
  }
  80% {
    -webkit-transform: rotateX(10deg) translateX(-25%);
            transform: rotateX(10deg) translateX(-25%);
  }
  100% {
    -webkit-transform: rotateX(10deg) translateX(0);
            transform: rotateX(10deg) translateX(0);
  }
}

@keyframes bookMove {
  30% {
    -webkit-transform: rotateX(10deg) translateX(-25%) translateY(0);
            transform: rotateX(10deg) translateX(-25%) translateY(0);
  }
  80% {
    -webkit-transform: rotateX(10deg) translateX(-25%);
            transform: rotateX(10deg) translateX(-25%);
  }
  100% {
    -webkit-transform: rotateX(10deg) translateX(0);
            transform: rotateX(10deg) translateX(0);
  }
}

.page-top, .page-bottom {
  width: 50%;
  height: 100%;

  position: absolute;
  right: 0;

  background-image: -webkit-gradient(linear,right top, left top,color-stop(92.4%, rgb(238, 238, 238)),to(rgb(180, 180, 180)));

  background-image: -webkit-linear-gradient(right,rgb(238, 238, 238) 92.4%,rgb(180, 180, 180) 100%);

  background-image: linear-gradient(to left,rgb(238, 238, 238) 92.4%,rgb(180, 180, 180) 100%);

  -webkit-transform-style: preserve-3d;

          transform-style: preserve-3d;
	-webkit-transform-origin: center left;
	        transform-origin: center left;
  -webkit-animation: flip-top 1s ease-in-out;
          animation: flip-top 1s ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}

.page-bottom {
  -webkit-backface-visibility: visible;
          backface-visibility: visible;
}

@-webkit-keyframes flip-top {
	0% {
    -webkit-transform: rotateY(0);
            transform: rotateY(0);
  }
  51% {
    z-index: 1000;
  }
	100% {
    -webkit-transform: rotateY(-180deg);
            transform: rotateY(-180deg);
    z-index: 1000;
	}
}

@keyframes flip-top {
	0% {
    -webkit-transform: rotateY(0);
            transform: rotateY(0);
  }
  51% {
    z-index: 1000;
  }
	100% {
    -webkit-transform: rotateY(-180deg);
            transform: rotateY(-180deg);
    z-index: 1000;
	}
}

.one {
  -webkit-animation-delay: 4.2s;
          animation-delay: 4.2s;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

.cover {
  margin-top: 0;
	width: 510px;
  height: auto;
}

.book-title {
  position: absolute;
  top: 35%;
  left: 35%;

  text-align: center;
  font-size: 8vh;
  line-height: 8.1vh;
  color: rgba(250, 215, 0, 0.5);
  text-shadow: 2px 2px 2px rgba(255,255,255,0.3);

}

.book-author {
  position: absolute;
  bottom: 15%;
  right: 25%;

  text-align: center;
  font-size: 3vh;
  line-height: 3.1vh;
  color: rgba(250, 215, 0, 0.5);
  text-shadow: 2px 2px 2px rgba(255,255,255,0.3);

}

.illustration {
  margin-left: 40px;
  margin-top: 40px;
	width: 420px;
  height: auto;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.text {
  padding: 40px;
  text-align: justify;
}

.hint {
  color: #404040;
  border: 5px double black;
  text-align: center;
  width: 420px;
  margin: 0 auto;
  padding: 10px;
}

.startBtn {
  cursor: pointer;
  color: #00bf00;
  -webkit-animation: pulse 0.5s ease-in-out;
          animation: pulse 0.5s ease-in-out;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;

  position: absolute;
  bottom: 40px;
  right: 40px;
}

.nextPageBtn {
  cursor: pointer;
  color: grey;

  position: absolute;
  bottom: 40px;
  right: 40px;
}

.nextPageBtn:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.gameOver {
  position: absolute;
  top: -500px;
  left: 15%;
  padding: 20px;

  border: 2px solid #000;
  color: #fff;
  background-color: hsla(0, 0%, 13%, 0.5);

  display: flex;
  flex-direction: column;
  align-items: center;

  -webkit-animation: goDown 0.5s linear 6.5s;

          animation: goDown 0.5s linear 6.5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes goDown {
  0%{
    top: -500px;
  }
  100%{
    top: 200px;
  }
}

@keyframes goDown {
  0%{
    top: -500px;
  }
  100%{
    top: 200px;
  }
}

.nextLevelBTN {
  color: #fff;
  background-color: transparent;
  align-self: flex-end;
}

.gameOver > p {
  font-size: 30px;
  line-height: 40px;
}



.game-field {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;

  background-color: black;

  -webkit-perspective: 1500px;

          perspective: 1500px;
}

.field, .gameInfo {
  box-sizing: border-box;
  height: 100%;
  border: 10px outset #7e4e1f;

  position: absolute;

  display: flex;
}

.field {
  width: 100%;
  top: 0;
  left: 0;

  flex-direction: row;
  flex-wrap: wrap;
  background-color: transparent;
}

.field-back {
  box-sizing: border-box;
  height: 100%;
  width: 70%;
  top: 0;

  overflow: hidden;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-clip: border-box;
  background-size: 70%;

  -webkit-transform: rotateY(180deg);

          transform: rotateY(180deg);
  -webkit-transform-origin: center left;
          transform-origin: center left;
  -webkit-animation: appearence 1s linear;
          animation: appearence 1s linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes appearence {
  100% {
    -webkit-transform: rotateY(0);
            transform: rotateY(0);
  }
}

@keyframes appearence {
  100% {
    -webkit-transform: rotateY(0);
            transform: rotateY(0);
  }
}

.goesTop {
  top: 0;
  left: 0;
  -webkit-animation: fieldGoesTop 3s linear 3s;
          animation: fieldGoesTop 3s linear 3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes fieldGoesTop {
  0% {
    top: 0;
    left: 0;
  }
  100% {
    top: -1500px;
    left: 0px;
  }
}

@keyframes fieldGoesTop {
  0% {
    top: 0;
    left: 0;
  }
  100% {
    top: -1500px;
    left: 0px;
  }
}


.gameInfo {
  width: 30%;
  top: 0;
  left: 70%;

  flex-direction: column;

  -webkit-transform: rotateY(-180deg);

          transform: rotateY(-180deg);
  -webkit-transform-origin: center right;
          transform-origin: center right;
  -webkit-animation: appearence 1s linear;
          animation: appearence 1s linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.cell, .level, .flash, .dash, .who, .turn, .score {
  box-sizing: border-box;
  padding-top: 1%;
  border: 5px inset #9e6226;

  background-color: #202020;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.cell {
  width: 10%;
  height: 10%;
}

.cellSelected {
  color: transparent;
  background-color: transparent;
}

.rowSelected, .colSelected {
  background-color: #404040;
}

.inrow:hover, .incol:hover {
  outline: 5px dashed blue;
  outline-offset: -10px;
  cursor: pointer;
  -webkit-animation: blinking 1s linear;
          animation: blinking 1s linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.halfTransparetCell {
  background-color: rgba(28, 28, 28, 0.5);
}

@-webkit-keyframes blinking {
  0% {
    outline: 5px dashed blue;
  }
  50% {
    outline: 5px dashed white;
  }
  100% {
    outline: 5px dashed blue;
  }
}

@keyframes blinking {
  0% {
    outline: 5px dashed blue;
  }
  50% {
    outline: 5px dashed white;
  }
  100% {
    outline: 5px dashed blue;
  }
}

.level {
  width: 100%;
  height: 5%;
  border-style: groove;
}

.current {
  height: 10%;
}

.flash {
  width: 100%;
  height: 40%;
  padding: 0;
}

.dash {
  width: 100%;
  height: 10%;
  padding: 0;
}

.who, .score {
  width: 30%;
  height: 100%;
}

.who, .current  {
  font-size: 2.3vw;
  color: #fff;
  text-shadow:
    3px 2px 3px rgba(0,0,0,1),
    3px 3px 5px rgba(0,0,0,0.8),
    3px 5px 9px rgba(0,0,0,0.6);
}

.who {
  border-style: groove;
}

.baby, .wimp, .middling, .strong, .hero {
  font-size: 2.5vh;
  color: #fff;
  text-shadow:
    2px 1px 2px rgba(0,0,0,1),
    2px 2px 3px rgba(0,0,0,0.8),
    2px 4px 5px rgba(0,0,0,0.6);
}

.selectedLevel {
  background-color: #ff0000;
}

.score {
  padding: 2% 2% 0 2%;
  background-color: #000;
  justify-content: flex-end;
}

.turn {
  width: 40%;
  height: 100%;
  padding-top: 2%;
  background-color: #000;
}

.turn-go {
  color: #00bf00;
  -webkit-animation: pulse 0.5s ease-in-out;
          animation: pulse 0.5s ease-in-out;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes pulse {
  0% {
    font-size: 6vh;
  }
  50% {
    font-size: 6.5vh;
  }
  100% {
    font-size: 6vh;
  }
}

@keyframes pulse {
  0% {
    font-size: 6vh;
  }
  50% {
    font-size: 6.5vh;
  }
  100% {
    font-size: 6vh;
  }
}

.turn-stop {
  color: #ff0000;
}

.author {
  height: 5%;
  font-size: 2vh;
  color: white;
  border-style: groove;
}

.interim {
  width: 100%;
  height: 100%;
  background-color: #000;
  position: relative;
}

.flash-img {
  width: 100%;
  height: 100%;
  background-color: #000;
}

.flash-finImg {
  width: auto;
  max-width: 100%;
  height: 100%;
  background-color: #000;
}

.winner {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  text-align: center;

  font-size: 50px;
  color: transparent;

  -webkit-animation: whoWin 1s linear 1s;

          animation: whoWin 1s linear 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes whoWin {
  0% {
    color: transparent;
  }
  100% {
    color: white;
  }
}

@keyframes whoWin {
  0% {
    color: transparent;
  }
  100% {
    color: white;
  }
}


* {
  margin: 0;
  padding: 0;
  background-color: transparent;
}

html {
  width: 100%;
  height: 100%;
}

body {
  width: 100%;
  height: 100%;
}

#root {
  width: 100%;
  height: 100%;
}

.App {
  width: 100%;
  height: 100%;
  font-family: 'Orbitron', sans-serif;
  font-size: 6vh;
  font-weight: bold;

  background-color: #000;

  overflow: hidden;
}

